import {
  ActionResult,
  ActionState,
  ActionCmdResult,
  ActionType,
  ActionsType,
  UnknownArgsActionType,
  NormalAction,
} from './types'
import Cmd, { CmdType, Sub } from './cmd'
import { set, merge, setDeep, setDeepMutable, get, isFn, noop, isPojo, clone } from './utils'
export * from './helpers'
export * from './types'
export { Cmd, CmdType, Sub, ActionResult, noop, isFn, isPojo }

export type Init<S, A> = () => S | [S, CmdType<A>] | InitObj<S, A>
export type InitObj<S, A> = {
  state: S
  cmd?: CmdType<A>
}
// todo: Remove back compatible optional ctx
export type View<S, A> = ((state: S, actions: A) => any)
export type Subscribe<S, A> = (state: S) => CmdType<A>
export type OnUpdate<S, A> = <M>(
  data: { prevAppState: S; nextAppState: S; msgData: M; action: string },
) => void
export type OnUpdateStart<S, A> = <M>(data: { action: string }) => void
export type Patch = <S, A>(path: string | string[], component: Component<S, A>, reuseState?: boolean) => Promise<any>

export interface AppProps<State, Actions> {
  init: Init<State, Actions>
  view: View<State, Actions>
  actions: ActionsType<State, Actions>
  subscribe?: Subscribe<State, Actions>
  // middlewares: ((action: MyAction<any, State, Actions>, key: string, path: string[]) => MyAction<any, State, Actions>)[],
  onRender?: (view: any) => void
  onUpdate?: OnUpdate<State, Actions>
  onUpdateStart?: OnUpdateStart<State, Actions>
  /**
   * Use mutable state, useful for high performance scenarios like graphics rendering, e.g. hydux-pixi
   */
  mutable?: boolean
}
/**
 * run action and return a normalized result ([State, CmdType<>]),
 * this is useful to write High-Order-Action, which take an action and return a wrapped action.
 * @param result result of `action(msg: Data)`
 * @param state
 * @param actions
 */
export function runAction<S, A, PS, PA>(
  result: ActionResult<S, A> | ((state: S, actions: A) => ActionResult<S, A>),
  state: S,
  actions: A,
  parentState?: PS,
  parentActions?: PA,
  appContext?: Context<any, any>
): ActionCmdResult<S, A> {
  let rst: any = result
  isFn(rst)
    && (rst = rst(state, actions, parentState, parentActions))
    && isFn(rst)
    && (rst = rst(actions))
  // action can be a function that return a promise or undefined(callback)
  if (
    rst === undefined ||
    (rst.then && isFn(rst.then))
  ) {
    return [state, Cmd.none]
  }
  if (rst instanceof Array) {
    return [rst[0] || state, rst[1] || Cmd.none]
  }
  return [rst, Cmd.none]
}

export function withParents<S, A, PS, PA, A1>(
  action: (a1: A1) => (s: S, a: A) => any,
  wrapper?: (
    action: (a1: A1) => ActionCmdResult<S, A>,
    parentState: PS,
    parentActions: PA,
    state: S,
    actions: A,
  ) => ActionResult<S, A>,
  parentState?: PS,
  parentActions?: PA,
)
export function withParents<S, A, PS, PA, A1, A2>(
  action: (a1: A1, a2: A2) => (s: S, a: A) => any,
  wrapper?: (
    action: (a1: A1, a2: A2) => ActionCmdResult<S, A>,
    parentState: PS,
    parentActions: PA,
    state: S,
    actions: A,
  ) => ActionResult<S, A>,
  parentState?: PS,
  parentActions?: PA,
)
export function withParents<S, A, PS, PA, A1, A2, A3>(
  action: (a1: A1, a2: A2, a3: A3) => (s: S, a: A) => any,
  wrapper?: (
    action: (a1: A1, a2: A2, a3: A3) => ActionCmdResult<S, A>,
    parentState: PS,
    parentActions: PA,
    state: S,
    actions: A,
  ) => ActionResult<S, A>,
  parentState?: PS,
  parentActions?: PA,
)
export function withParents<S, A, PS, PA, A1, A2, A3, A4>(
  action: (a1: A1, a2: A2, a3: A3, a4: A4) => (s: S, a: A) => any,
  wrapper?: (
    action: (a1: A1, a2: A2, a3: A3, a4: A4) => ActionCmdResult<S, A>,
    parentState: PS,
    parentActions: PA,
    state: S,
    actions: A,
  ) => ActionResult<S, A>,
  parentState?: PS,
  parentActions?: PA,
)
export function withParents<S, A, PS, PA, A1, A2, A3, A4, A5>(
  action: (a1: A1, a2: A2, a3: A3, a4: A4, a5: A5) => (s: S, a: A) => any,
  wrapper?: (
    action: (a1: A1, a2: A2, a3: A3, a4: A4, a5: A5) => ActionCmdResult<S, A>,
    parentState: PS,
    parentActions: PA,
    state: S,
    actions: A,
  ) => ActionResult<S, A>,
  parentState?: PS,
  parentActions?: PA,
)
/**
 * Wrap a child action with parentState, parentActions.
 * @param action The action to be wrapped
 * @param wrapper
 * @param parentState
 * @param parentActions
 */
export function withParents<S, A, PS, PA>(
  action: UnknownArgsActionType<S, A>,
  wrapper?: (
    action: NormalAction<any, S, A>,
    parentState: PS,
    parentActions: PA,
    state: S,
    actions: A,
  ) => ActionResult<S, A>,
  parentState?: PS,
  parentActions?: PA,
): any {
  if (!wrapper) {
    return action
  }
  const wrapped = (state: S, actions: A, parentState: PS, parentActions: PA) => {
    const nactions = (...args) => runAction(action(...args), state, actions)
    return wrapper(nactions, parentState, parentActions, state, actions)
  }
  return wrapped
}
/**
 * @deprecated Deprecated for `withParents`
 */
export const wrapActions = withParents

export interface Component<State, Actions> {
  init: Init<State, Actions>
  view: View<State, Actions>
  actions: ActionsType<State, Actions>
}

export interface Context<State, Actions, RenderReturn = any> {
  actions: Actions
  state: State
  init: Init<State, Actions>
  view: View<State, Actions>
  subscribe?: Subscribe<State, Actions>
  onRender?: ((view: any) => RenderReturn)
  onUpdate?: OnUpdate<State, Actions>
  onUpdateStart?: OnUpdateStart<State, Actions>
  /** Patch a component in runtime, used for code-splitting */
  patch: Patch
  render(state?: State): RenderReturn
}
export type App<State, Actions> = (props: AppProps<State, Actions>) => Context<State, Actions, any>
export type Enhancer<S, A> = (app: App<S, A>) => App<S, A>

function isInitObj<S, A>(res: ReturnType<Init<S, A>>): res is InitObj<S, A> {
  const keys = Object.keys(res).join('|')
  return keys === 'state' || keys === 'state|cmd'
}

export function normalizeInit<S, A>(initResult: ReturnType<Init<S, A>>): Required<InitObj<S, A>> {
  if (initResult instanceof Array) {
    return {
      state: initResult[0],
      cmd: initResult[1]
    }
  }
  if (isInitObj(initResult)) {
    return {
      state: initResult.state,
      cmd: initResult.cmd || Cmd.none,
    }
  }
  return {
    state: initResult as S,
    cmd: Cmd.none
  }
}

export function runCmd<A>(cmd: CmdType<A>, actions: A) {
  return cmd.map(sub => sub(actions))
}

export function app<State, Actions>(props: AppProps<State, Actions>): Context<State, Actions> {
  // const appEvents = props.events || {}
  let appActions = {} as Actions
  const appSubscribe = props.subscribe || (_ => Cmd.none)
  const render = props.onRender || noop
  // const appMiddlewares = props.middlewares || []
  let { state: appState, cmd } = normalizeInit(props.init())
  init(appState, appActions, props.actions, [])
  runCmd(cmd, appActions)
  appRender(appState)
  appSubscribe(appState).forEach(sub => sub(appActions))

  const appContext = {
    // getter should before spread operator,
    // otherwise it would be copied and becomes normal property
    get state() {
      return appState
    },
    ...props,
    actions: appActions,
    render: appRender,
    patch<S, A>(path: string, comp: Component<S, A>, reuseState = false): Promise<any> {
      reuseState = reuseState && appState[path]
      let { state, cmd } = normalizeInit(comp.init())
      let actions = appActions[path]
      if (!actions) {
        actions = appActions[path] = {}
        init(state, actions, comp.actions as any, [path])
      }
      if (!reuseState) {
        appState = setDeep([path], state, appState)
      }
      appState = setDeep(['lazyComps', path], comp, appState)
      appRender(appState)
      return reuseState
        ? Promise.resolve()
        : Promise.all(runCmd(cmd, actions))
    }
  }

  return appContext

  function appRender(state = appState) {
    if (state !== appState) {
      appState = state
    }
    let view = props.view(appState, appActions)
    if (isFn(view)) {
      view = view(appActions)
    }
    return render(view)
  }

  function init(
    state,
    actions: any,
    from: ActionsType<State, Actions> | ActionType<any, State, Actions>,
    path: string[],
  ) {
    for (const key in from) {
      if (/^_/.test(key)) {
        continue
      }
      const subFrom = from[key]
      if (isFn(subFrom)) {
        actions[key] = function(...msgData) {
          state = get(path, appState)
          // action = appMiddlewares.reduce((action, fn) => fn(action, key, path), action)
          let nextState = state
          let nextAppState = appState
          let cmd = Cmd.none
          let parentState
          let parentActions
          const actionResult = subFrom(...msgData)
          if (isFn(actionResult) && actionResult.length > 2) {
            let pLen = path.length - 1
            parentActions = get(path, appActions, pLen)
            parentState = get(path, appState, pLen)
          }
          [nextState, cmd] = runAction(
            actionResult,
            state,
            actions,
            parentState,
            parentActions,
          )

          if (props.onUpdate) {
            if (props.mutable) {
              nextAppState = setDeepMutable(
                path,
                state !== nextState
                  ? set(state, nextState)
                  : state,
                appState,
              )
            } else {
              nextAppState = setDeep(path, merge(state, nextState), appState)
            }
            props.onUpdate({
              prevAppState: appState,
              nextAppState,
              msgData: subFrom.length ? msgData : [],
              action: path.join('.') + '.' + key,
            })
          }
          if (props.mutable) {
            appState = setDeepMutable(
              path,
              state !== nextState
                ? set(state, nextState)
                : state,
              appState,
            )
            appRender(appState)
          } else if (nextState !== state) {
            if (props.onUpdate) {
              appState = nextAppState
            } else {
              appState = setDeep(path, merge(state, nextState), appState)
            }
            appRender(appState)
          }
          return runCmd(cmd, actions)
        }
      } else if (typeof subFrom === 'object' && subFrom) {
        init(
          state[key] || (state[key] = {}),
          (actions[key] = clone(subFrom)),
          subFrom,
          path.concat(key),
        )
      }
    }
  }
}

export default app



